.todo__tasks {
  margin-top: 30px; }
  .todo__tasks h2 {
    font-family: Montserrat;
    font-weight: bold;
    font-size: 22px;
    color: #c9d1d3;
    position: absolute;
    left: 50%;
    top: 65%; }
  .todo__tasks .done {
    opacity: 0.4;
    text-decoration: line-through; }
  .todo__tasks .task {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer; }
    .todo__tasks .task:hover .delete_task {
      opacity: 1; }
    .todo__tasks .task span {
      margin-left: 15px;
      border: 0;
      font-size: 16px; }
    .todo__tasks .task input {
      background: #ffffff;
      border: 1px solid #efefef;
      box-sizing: border-box;
      border-radius: 4px;
      padding: 8px 12px;
      font-size: 16px;
      margin-left: 15px; }
      .todo__tasks .task input:focus {
        border-color: #dbdbdb; }
    .todo__tasks .task .checkbox input {
      display: none; }
    .todo__tasks .task .checkbox svg {
      transition: opacity 0.15s ease-in-out; }
      .todo__tasks .task .checkbox svg path {
        stroke: #f2f2f2; }
    .todo__tasks .task .checkbox input:checked + label {
      background-color: #4dd599;
      border-color: #4dd599; }
      .todo__tasks .task .checkbox input:checked + label svg {
        opacity: 1; }
        .todo__tasks .task .checkbox input:checked + label svg path {
          stroke: #fff; }
    .todo__tasks .task .checkbox:hover label {
      background-color: #f2f2f2;
      border-color: #f2f2f2; }
      .todo__tasks .task .checkbox:hover label svg {
        opacity: 1; }
        .todo__tasks .task .checkbox:hover label svg path {
          stroke: #b2b2b2; }
    .todo__tasks .task .checkbox label {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #e8e8e8;
      border-radius: 30px;
      width: 20px;
      height: 20px;
      cursor: pointer;
      transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out; }
      .todo__tasks .task .checkbox label svg {
        opacity: 0; }
    .todo__tasks .task .delete_task {
      display: flex;
      opacity: 0;
      transition: opacity 0.15s ease-in-out;
      margin-left: 70px; }
      .todo__tasks .task .delete_task div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        background-color: #fafbfc;
        border-radius: 6px;
        margin-left: 5px;
        cursor: pointer; }
        .todo__tasks .task .delete_task div:hover {
          background-color: #eef1f4; }
          .todo__tasks .task .delete_task div:hover svg {
            opacity: 0.9; }
        .todo__tasks .task .delete_task div svg {
          width: 11px;
          height: 11px;
          opacity: 0.4; }
