.todo__tasks {
margin-top: 30px;
  h2 {
    font-family: Montserrat;
    font-weight: bold;
    font-size: 22px;
    color: #c9d1d3;
    position: absolute;
    left: 50%;
    top: 65%;
  }

  .done {
    opacity: 0.4;
    text-decoration: line-through;
  }

  .task {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer;


    &:hover .delete_task {
      opacity: 1;
    }



    span {
      margin-left: 15px;
      border: 0;
      font-size: 16px;
    }

    input {
      background: #ffffff;
      border: 1px solid #efefef;
      box-sizing: border-box;
      border-radius: 4px;
      padding: 8px 12px;
      font-size: 16px;
      margin-left: 15px;

      &:focus {
        border-color: #dbdbdb;
      }
    }

    .checkbox {
      input {
        display: none;
      }

      svg {
        transition: opacity 0.15s ease-in-out;
        path {
          stroke: #f2f2f2;
        }
      }

      input:checked + label {
        background-color: #4dd599;
        border-color: #4dd599;

        svg {
          opacity: 1;
          path {
            stroke: #fff;
          }
        }
      }

      &:hover {
        label {
          background-color: #f2f2f2;
          border-color: #f2f2f2;
          svg {
            opacity: 1;
            path {
              stroke: #b2b2b2;
            }
          }
        }
      }

      label {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid #e8e8e8;
        border-radius: 30px;
        width: 20px;
        height: 20px;
        cursor: pointer;
        transition: background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out;

        svg {
          opacity: 0;
        }
      }
    }

    .delete_task {
      display: flex;
      opacity: 0;
      transition: opacity 0.15s ease-in-out;
      margin-left: 70px;

      div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        background-color: lighten(#f4f6f8, 2%);
        border-radius: 6px;
        margin-left: 5px;
        cursor: pointer;

        &:hover {
          background-color: darken(#f4f6f8, 2%);
          svg {
            opacity: 0.9;
          }
        }

        svg {
          width: 11px;
          height: 11px;
          opacity: 0.4;
        }
      }
    }
  }
}